<template>
  <div class="footprint">
    <hx-Return :title="title"></hx-Return>
    <div class="title">
      <div>
        共
        <span>{{num}}</span>条记录
      </div>
    </div>
    <ul class="find-similar-list">
      <li v-for="(item,index) in list" :key="index">
        <div class="similar">
          <img :src="item.url" @click="add(index)" />
        </div>
        <span class="similar-product-text">
          <img src="../assets/images/618.png" alt />
          {{item.title}}
        </span>
        <p class="similar-product-info">
          <span class="similar-product-price">
            ¥&nbsp;
            <span class="big-price">{{item.price}}</span>
          </span>
          <van-icon name="shopping-cart" @click="toNotify(item)" />
        </p>
      </li>
    </ul>
  </div>
</template>

<script>
import hxReturn from "../components/hxReturn";
export default {
  data() {
    return {
      title: "我的足迹",
      num: 0,
      list: []
    };
  },
  methods: {
    toNotify(item) {
      this.$notify({
        type: "danger",
        message: "提交订单成功",
        duration: 500
      });
      let obj = [
        {
          num: 1,
          specifications: "套餐一",
          title: item.title,
          url:item.url,
        }
      ];
      this.$store.state.tobepaidlist = { obj: obj, price: item.price * 100 };
      this.$router.push({ name: "placeorder" });
    },
    add(index){
      this.$router.push({ name: "productdetails", query: this.list[index] });
    }
  },
  created() {
    this.list = this.$store.state.footprintlist;
    // console.log(this.list);
    this.num = this.list.length
    //隐藏导航
    this.$store.state.vantTab = false;
  },
  // 切换组件生周期钩子，销毁函数
  beforeDestroy() {
    this.$store.state.vantTab = true;
  },

  components: {
    hxReturn
  }
};
</script>

<style lang="less" scoped>
.footprint {
  height: calc(100% - 44px);
  width: 100%;
  padding-top: 44px;
  .title {
    height: 50px;
    line-height: 50px;
    padding-left: 10px;
    span {
      color: red;
      margin: 0 4px 0 4px;
    }
  }
  .find-similar-list {
    margin-left: 13px;
    width: calc(100% - 26px);
    > li {
      width: calc(50% - 5px);
      height: 250px;
      padding-top: 8px;
      float: left;
      background-color: #ffffff;
      > .similar {
        width: 100%;
        > img {
          height: 100%;
          width: 100%;
        }
      }
      > .similar-product-text {
        padding-left: 5px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        font-size: 13px;
        width: 100%;
        img {
          width: 31px;
          height: 13px;
          margin-right: 10px;
        }
      }
      > .similar-product-info {
        padding-left: 5px;
        > .similar-product-price {
          color: red;
          font-size: 12px;
          padding-top: 15px;
          > .big-price {
            font-size: 17px;
          }
        }
        .van-icon {
          float: right;
          position: relative;
          top: 5px;
          right: 5px;
          font-size: 25px;
          color: red;
        }
      }
    }
    > li:nth-child(2n - 1) {
      margin: 1px 1px 2px 0;
    }
    > li:nth-child(2n) {
      margin: 1px 0 2px 7px;
    }
  }
}
</style>